.box{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 100%;
    height: 100%;
    overflow-y: scroll;

    & .image{
        max-height: 390px;
        min-height: 200px;
        z-index: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 100%;
        background-color: rgb(234, 234, 234);
        margin-bottom: 12px;
        padding-block: 10px;
        box-shadow: inset 0 0 13px black;
        border-radius: 10px;


        & .image-box{
            position: relative;
            justify-content: center;
            align-items: center;
            margin-inline: auto;
            display: block;
            height: 100%;
            width: fit-content;
        }

        & img{
            position: relative;
            object-fit: contain;
            display: flex;
            border-radius: 8px;
            height: 100%;
            max-width: 100%;
            overflow: hidden;
        }

    }

    & .info-data{
        background-color: whitesmoke;
        padding: 10px;
        margin: auto;
        border-radius: 10px;
        height: 100%;
        width: 95%;
        text-align: center;
        align-items: center;
        overflow-y: scroll;

        & h3{
            font-size: 30px;
            color: black;
            text-transform:uppercase;
            font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        }
        & p{
            font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
            font-size: 20px;
            text-align: left;
            padding: 10px;
            letter-spacing: 1px;
        }
        & .line{
            height: 4px;
            margin-inline: auto;
            width: 80%;
            background-color: rgb(25, 25, 25);
            margin-bottom: 8px;
            border-radius: 4px;
        }
    }

    & .tags{
        position: relative;
        bottom: 0;
        display: grid;
        grid-template-columns: auto 1fr;
        gap: 30px;
        font-size: 30px;
        padding-block: 15px;
        padding-left: 5%;
        color: whitesmoke;
        align-items: center;
        min-height: 10%;
        height: fit-content;
        overflow: hidden;
        width: 100%;

        & div a:visited{
            text-decoration: none;
            cursor: pointer;
            color: whitesmoke;
            align-items: center;
            margin-block: auto;
        }
        & div a{
            text-decoration: none;
            cursor: pointer;
            color: whitesmoke;
            align-items: center;
            margin-block: auto;
        }

        & div p{
            background-color: whitesmoke;
            width: fit-content;
            color: rgb(44, 44, 44);
            border-radius: 8px;
            margin-block: auto;
            text-align: center;
            padding-inline: 10px;
            padding-block: 5px;
            font-weight: 700;
            font-size: 15px;
        }
    }
}